import React from 'react';
import {TOGGLE_X_BUTTON} from '../../constants';
function  OverlayCloseButton ({ data, removeOverlay })  {
    let overlayCName = 'overlay-close';
    if (`${data[TOGGLE_X_BUTTON]}`) {
        overlayCName += ' toggle-x';
    }
    return (
        <button type="button" className={overlayCName} onClick={(e) => removeOverlay()}/>
    );
};

export default OverlayCloseButton;
